.mapBG {
    padding: 10px;
    position: absolute;
    top: 200px;
    width: 470px;
    height: 570px;
    margin: 10px;
    margin-left: 250px;
    overflow: scroll;
}

.textBG {
    background-color:hsla(39,30%,89%,0.8);
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 10px;
    position: absolute;
    top: 30%;
    left: 101%;
    width: 445px;
    height: 670px;
    overflow: scroll;
}

.textStore {
  background-color:hsla(39,30%,89%,0.8);
  border-style: solid;
  border-width: 1px;
  border-radius: 5px;
  padding: 10px;
  position: absolute;
  top: 60%;
  left: 70%;
  width: 445px;
  height: 670px;
  overflow: scroll;
}

#castle {
  background-color: rgba(0,0,0,0);
  width: 500px;
  height: 500px;
  position: absolute;
  top: 280px;
  right: 250px;
}

.scroll-log {
  overflow: scroll;
  height: 200px;
}



.show {
    display: block;
}

.hide {
    display: none;
}

.show1 {
  display: block;
}

.hide1 {
  display: none;
}

#type{
  background-color:rgba(0,0,0,0.4);
  border-radius: 0px 0px 10px 10px;
  height: 1000px;
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  opacity: 3;
}

#kingYes, #kingNo{ 
  border-radius: 3px;
  border-color: #b56262;
  margin: 2px;
  background-color: #d38888;
  color: #9b5051;
  height: 40px;
  width: 50px;
  
}

.kingBtns {
  position: absolute;
  top: 63%;
  left: 40%;
}

.game-btn {
  border-radius: 3px;
  border-color: #533b2a;
  border-style: solid;
  border-width: 1px;
  padding: 5px;
  margin: 2px;
  background-color: #7d5f40;
  color: #f1dec7;
}


.game-btn:hover {
  border-radius: 3px;
  border-color: #f1dec7;
  border-style: solid;
  border-width: 1px;
  margin: 2px;
  background-color: #533b2a;
  color: #7d5f40;
}

.game-btn:active {
  border-radius: 3px;
  border-color: #7d5f40;
  border-style: solid;
  border-width: 1px;
  margin: 2px;
  background-color: #f1dec7;
  color:	#533b2a;
}

.appear-image {
    height: 80px;
    position: absolute;
    top: 480px;
    right: 450px;
}

.avatar-image {
  height: 80px;
  position: absolute;
  top: 600px;
  right: 450px;
}

.poof-image {
  height: 400px;
    position: absolute;
    top: 280px;
    right: 250px;
}

div::-webkit-scrollbar {
    width: 1em;
  }
   
div::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  }

div::-webkit-scrollbar-corner {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  }
   
div::-webkit-scrollbar-thumb {
    background-color: #c0a083;
    outline: 1px solid slategrey;
  }


#hangman{
  background-color:rgba(0,0,0,0.4);
  border-radius: 0px 0px 10px 10px;
  height: 1000px;
  width: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  opacity: 3;
}


canvas {
    background-color: black;
    border: 5px white solid;
    position: absolute;
    left: 32%;
    top: 5%;
}